.App {
  font-size: large;
  background-color: rgba(247, 246, 246, 0.886);
  min-height: 100vh;
  color: blue;
}

.userCard {
  color: rgb(35, 37, 37);
  display: flex;
  justify-content: space-around;
  margin-top: 5px;
  background-color: rgb(208, 163, 230);
  border-radius: 3px;
  align-items: baseline;
  box-shadow: 8px 12px 18px rgb(104, 77, 77);
}

.userCard h3 {
  font-weight: bold;
}
.userCard p {
  font-weight: lighter;
}

.userCard button {
  font-weight: lighter;

  color: black;
}

.header {
  background-color: rgb(54, 52, 52);
  margin-bottom: 5px;
}

.header h1 {
  color: white;
}

.navbar {
  display: flex;
  justify-content: space-around;
  padding: 15px;
}

.userCard .userCard-link {
  display: flex;
  justify-content: space-evenly;
  flex-direction: row;
  padding: 0 50px 0 50px;
  padding: 0px 60px 0px 60px;
  align-items: baseline;
  margin-left: 0px;
  float: left;
}

.userCard-link img {
  width: 50px;
  float: left;
  margin-left: 0px;
}

.img{
  width: 80px;
  height: auto;
}
